import React, { useCallback } from 'react';
import { WhiteModal } from 'shared/components/Elements';
import EntityForm from 'modules/entity/forms';
import { useAppState } from 'shared/state';
import { BranchFormElements } from './BranchFormElements';
import { Branch } from '../../../../BusinessManagement/components/BusinessLocations';
import { useNotistack } from 'shared/hooks';
import { useTranslation } from 'react-i18next';

type Props = {
	handleModalOpen: (item: Branch | null) => void;
	open: Branch | null;
};

export function BranchEditModal({ handleModalOpen, open }: Props) {
	const { t } = useTranslation();
	const closeModal = useCallback(() => handleModalOpen(null), []);
	const { companyId } = useAppState();
	const { showNotification } = useNotistack();

	return (
		<div className="relative">
			<WhiteModal handleModalOpen={closeModal} open={Boolean(open)} scrollable noPadding>
				<EntityForm.Main
					id={open?.id}
					entity="branches"
					name={`AllBranches-${companyId}`}
					url={`/${companyId}/my/company-branches/${open?.id}/`}
					method="put"
					primaryKey="id"
					sendAsFormData={false}
					normalizeData={(data: any) => data}
					prependData
					fields={[
						{
							name: 'name',
							required: true,
							value: open?.name,
							onSubmitKey: ''
						},
						{
							name: 'address',
							required: true,
							value: open?.address,
							onSubmitKey: ''
						},
						{
							name: 'city',
							required: true,
							value: open?.city,
							onSubmitKey: ''
						},
						{
							name: 'phone',
							value: open?.phone,
							onSubmitKey: ''
						},
						{
							name: 'state_name',
							required: false,
							value: open?.state_name,
							onSubmitKey: ''
						},
						{
							name: 'country_name',
							required: true,
							value: open?.country_name,
							onSubmitKey: ''
						},
						{
							name: 'zipcode',
							value: open?.zipcode,
							onSubmitKey: ''
						},
						{
							name: 'lat',
							value: open?.lat,
							type: 'number',
							onSubmitKey: '',
							onSubmitValue: (value) => Number(value).toFixed(7)
						},
						{
							name: 'lng',
							value: open?.lng,
							type: 'number',
							onSubmitValue: (value) => Number(value).toFixed(7),
							onSubmitKey: ''
						}
					]}
					onSuccess={(response, resetForm): any => {
						closeModal();
						resetForm();
						showNotification({ message: t('update_success'), variant: 'success' });
					}}
					onError={(error, resetForm): any => {
						showNotification({ message: t('something_went_wrong'), variant: 'error' });
						resetForm();
					}}
				>
					{({ isSubmitting, values, setFieldValue, errors, setFieldError, resetForm }) => {
						return (
							<BranchFormElements
								values={values}
								setFieldValue={setFieldValue}
								setFieldError={setFieldError}
								isSubmitting={isSubmitting}
								errors={errors}
								isCreate={false}
								branch={open}
								closeModal={closeModal}
							/>
						);
					}}
				</EntityForm.Main>
			</WhiteModal>
		</div>
	);
}
