import React, { useEffect } from 'react';
import { EntytyFormInstanceProps } from 'shared/typings/formik.types';
import { Field } from 'formik';
import { KntInputField, KntPhoneInputField } from 'shared/components/Fields';
import { KntButton, Loader } from 'shared/components';
import { GoogleMap, Marker } from '@react-google-maps/api';
import { useBusinessProfile } from '../../BusinessProfile/hooks';
import isEmpty from 'lodash/isEmpty';
import { scrollIntoDomElement } from 'shared/services';
import { Branch } from '../../../../BusinessManagement/components/BusinessLocations';
import { useDidMountEffect } from 'shared/hooks';
import { useAddressFieldUpdate } from '../../hooks';
import { useTranslation } from 'react-i18next';

const containerStyle = {
	width: '100%',
	height: '400px'
};

type Props = {
	values: any;
	isCreate?: boolean;
	branch?: Branch | null;
	closeModal: () => void;
	formValuesSetter?: (fieldValues: string[]) => void;
} & EntytyFormInstanceProps;

export function BranchFormElements({
	setFieldValue,
	isSubmitting,
	isCreate,
	errors,
	setFieldError,
	values,
	branch,
	closeModal,
	resetForm,
	formValuesSetter = () => null
}: Props) {
	const { t } = useTranslation();
	const { searchInputRef, onLoad, onUnmount, onMarkerDrag, isLoaded, placeAddress, selected, map } =
		useBusinessProfile();
	//Update Address when typing manually(not selecting from place search)
	useAddressFieldUpdate({ setFieldValue, searchInputRef });
	// setting address fields
	useEffect(() => {
		if (!isEmpty(placeAddress)) {
			setFieldValue('city', placeAddress.city);
			setFieldValue('address', placeAddress.plain());
			setFieldValue('state_name', placeAddress.state);
			setFieldValue('country_name', placeAddress.country);
			setFieldValue('zipcode', placeAddress.zip);
			setFieldValue('lat', Number(placeAddress?.lat).toFixed(9));
			setFieldValue('lng', Number(placeAddress?.lng).toFixed(9));
		}
	}, [placeAddress]);

	useDidMountEffect(() => {
		if (selected && 'lat' in selected && 'lng' in selected) {
			setFieldValue('lat', Number(selected?.lat).toFixed(9));
			setFieldValue('lng', Number(selected?.lng).toFixed(9));
		}
	}, [selected]);

	//reset sub_industry on industry reset or change
	useEffect(() => {
		if (!isEmpty(errors)) {
			scrollIntoDomElement('branch-form');
		}
	}, [errors]);

	//Just to fix scrolling top when there is error on submit
	useEffect(() => {
		if (isSubmitting && !isEmpty(errors)) {
			Object.keys(errors as object).forEach((field) => setFieldError(field, false));
		}
	}, [isSubmitting]);

	//Update address field
	useEffect(() => {
		if (searchInputRef.current && branch) {
			(searchInputRef.current as HTMLInputElement).value = branch?.address as string;
		}
	}, [searchInputRef, branch]);

	//Fix map center on component load
	useEffect(() => {
		if (isLoaded && map) {
			const center = new google.maps.LatLng(
				Number(values?.lat) as number,
				Number(values?.lng) as number
			);
			(map as GoogleMap).panTo(center);
		}
	}, [isLoaded, map]);

	useEffect(() => {
		if (isCreate) {
			const checkableFields = ['city', 'name', 'address', 'city', 'country_name', 'state_name'];

			const fieldValues = Object.keys(values).filter((field) => {
				const fieldHasValue = Array.isArray(values[field]) ? values[field].length : values[field];
				if (fieldHasValue && checkableFields.includes(field)) {
					return values[field];
				}
			});
			formValuesSetter(fieldValues);
		}
	}, [values]);

	return (
		<div className={'branch-form lg:w-[948px]'} id={'branch-form'}>
			<div className="branch-form__header flex-center-between -mt-5 border-b border-kgrey-dark-1/30 px-6 py-4">
				<div className="branch-form__heading font-base font-medium">
					{isCreate ? t('create_new_branch') : t('update_branch')}
				</div>
				{isCreate ? (
					<div
						className="branch-form__reset cursor-pointer text-sm text-kgrey transition hover:text-kdark"
						onClick={resetForm}
					>
						{t('clear')}
					</div>
				) : null}
			</div>
			<div className="w-100 p-6 lg:grid lg:grid-cols-2 lg:gap-x-6">
				<div className="col-1">
					<Field
						label={t('branch_name')}
						name="name"
						component={KntInputField}
						placeholder={t('enter_branch_name')}
					/>
					<div className="mb-4">
						{/* eslint-disable-next-line jsx-a11y/label-has-associated-control */}
						<label
							htmlFor="address"
							className="ml-2 mb-2 block text-sm font-light text-kgrey-menuText"
						>
							{t('address')}
						</label>
						<input
							id="xyz123"
							name="xyz123"
							autoComplete="xyz123"
							type="search"
							ref={searchInputRef}
							className="w-full rounded-lg border border-transparent bg-kgrey-light-grey-1
							 px-6 py-3.5 text-xs placeholder-current placeholder-kgrey outline-none
							  transition hover:border-primary focus:border-primary focus:shadow-lg dark:bg-kdark"
						/>
					</div>
					<Field
						label={t('city')}
						name="city"
						component={KntInputField}
						placeholder={t('enter_city')}
					/>
					<Field
						label={t('state')}
						name="state_name"
						component={KntInputField}
						placeholder={t('enter_state')}
					/>
					<div className="react-select-dropdown mb-4">
						<div className="ml-2 mb-2 block text-sm font-light text-kgrey-menuText">
							<span>{t('map')}</span>
						</div>
						{isLoaded ? (
							<GoogleMap
								mapContainerStyle={containerStyle}
								center={{
									lat: Number(values?.lat),
									lng: Number(values?.lng)
								}}
								zoom={10}
								onLoad={onLoad}
								onUnmount={onUnmount}
								onClick={onMarkerDrag}
							>
								{/* Child components, such as markers, info windows, etc. */}
								<Marker
									draggable
									position={{
										lat: Number(selected?.lat) || Number(values?.lat),
										lng: Number(selected?.lng) || Number(values?.lng)
									}}
									onDragEnd={onMarkerDrag}
								/>
							</GoogleMap>
						) : (
							<p>There is problem with loading map or location has not been set.</p>
						)}
					</div>
					{/*.col-1 end*/}
				</div>
				<div className="col-2">
					<Field
						label={t('zip_code')}
						name="zipcode"
						component={KntInputField}
						placeholder={t('enter_zip_code')}
					/>
					<Field
						label={t('country')}
						name="country_name"
						component={KntInputField}
						placeholder={t('enter_country')}
					/>
					<Field
						label={t('phone_number')}
						name="phone"
						component={KntPhoneInputField}
						placeholder={t('enter_company_phone_number')}
						enableSearch
						onChangeHandler={setFieldValue}
					/>
				</div>
			</div>
			<div className="branch-form__footer flex justify-end px-6 pt-4 pb-9">
				<KntButton
					className={'mr-5 border border-kgrey text-kgrey'}
					color="default"
					onClick={closeModal}
				>
					{t('cancel')}
				</KntButton>
				<KntButton
					className={'dark:text-white dark:hover:bg-primary-300 dark:focus:ring-primary-300'}
					type="submit"
					isSubmitting={isSubmitting}
					color="primary"
				>
					{isCreate ? t('create') : t('update')}
				</KntButton>
			</div>
			{isSubmitting ? (
				<div className="absolute inset-0 flex items-center justify-center bg-white bg-opacity-70">
					<Loader />
				</div>
			) : null}
		</div>
	);
}

BranchFormElements.defaultProps = {
	isCreate: true,
	branch: null,
	// eslint-disable-next-line @typescript-eslint/no-empty-function
	resetForm: () => {}
};
