import React, { useState } from 'react';
import { useAppState } from 'shared/state';
import { WhiteModal, Button, DialogActions, DialogTitle } from 'shared/components/Elements';
import { ErrorBoundary, Loader } from 'shared/components';
import { useEntityForm } from 'modules/entity/hooks/useEntityForm';
import { Branch } from '../../../../BusinessManagement/components/BusinessLocations';
import { useNotistack, usePageChangeAfterEntityDelete } from '../../../../../shared/hooks';
import { Trans } from 'react-i18next';

type Props = {
	handleModalOpen: (item: Branch | null) => void;
	open: Branch | null;
};

function BranchDeleteModalInner({ handleModalOpen, open }: Props) {
	const { companyId } = useAppState();
	const [loading, setLoading] = useState(false);
	const { showNotification } = useNotistack();

	const { forwardToPreviousPage } = usePageChangeAfterEntityDelete({
		pageKey: 'page',
		entity: 'branches',
		entityName: `AllBranches-${companyId}`
	});

	const { handleRequest: handleDeleteRequest } = useEntityForm({
		entity: 'branches',
		name: `AllBranches-${companyId}`,
		method: 'delete',
		url: `/${companyId}/my/company-branches/${open?.id}/`,
		deleteData: true,
		id: open?.id,
		cb: {
			success: (data) => {
				handleModalOpen(null);
				setLoading(false);
				showNotification({ message: 'Successfully deleted', variant: 'success' });
				forwardToPreviousPage();
			},
			error: (error) => {
				setLoading(false);
			},
			// eslint-disable-next-line @typescript-eslint/no-empty-function
			finally: () => {}
		}
	});

	const rejectRequest = (branch: Branch) => {
		setLoading(true);
		handleDeleteRequest();
	};

	const handleDelete = () => {
		if (open) rejectRequest(open);
	};

	const closeModal = () => {
		handleModalOpen(null);
	};

	return (
		<div className="relative">
			<WhiteModal handleModalOpen={closeModal} open={Boolean(open)}>
				<DialogTitle id="alert-dialog-title">
					<Trans i18nKey="branch_delete_confirmation">
						Are you sure to delete <strong> {{ branchName: open?.name }} </strong>?
					</Trans>
				</DialogTitle>
				<DialogActions>
					<Button onClick={closeModal} autoFocus variant="contained">
						<Trans i18nKey="cancel">Cancel</Trans>
					</Button>
					<Button onClick={handleDelete} variant="contained" color="secondary">
						<Trans i18nKey="reject">Reject</Trans>
					</Button>
				</DialogActions>
				{loading && (
					<div className="absolute inset-0 flex items-center justify-center bg-white bg-opacity-70">
						<Loader />
					</div>
				)}
			</WhiteModal>
		</div>
	);
}

export function BranchDeleteModal(props: Props) {
	return (
		<ErrorBoundary>
			<BranchDeleteModalInner {...props} />
		</ErrorBoundary>
	);
}
