import { RefObject, useCallback, useEffect } from 'react';
import { FormikSetFieldValueType } from 'shared/typings';

type Props = {
	setFieldValue: FormikSetFieldValueType;
	searchInputRef: RefObject<HTMLInputElement>;
	field?: string;
};

export function useAddressFieldUpdate({ setFieldValue, searchInputRef, field = 'address' }: Props) {
	const onAddressChange = useCallback(function (e: Event) {
		const value = (e.target as HTMLInputElement).value;
		setFieldValue(field, value);
	}, []);

	//Update address typing by hand
	useEffect(() => {
		const inputCopy = searchInputRef.current;
		if (searchInputRef.current) {
			(searchInputRef.current as HTMLInputElement).addEventListener('change', onAddressChange);
		}

		return () => {
			(inputCopy as unknown as HTMLInputElement).removeEventListener('change', onAddressChange);
		};
	}, [searchInputRef, onAddressChange]);
}
