import React, { useCallback } from 'react';
import { WhiteModal } from 'shared/components/Elements';
import EntityForm from 'modules/entity/forms';
import { useAppState } from 'shared/state';
import { BranchFormElements } from './BranchFormElements';

type Props = {
	handleModalOpen: (isVisible: boolean) => void;
	open: boolean;
	onPopupClose: any;
	hasToShowGuard: () => void;
	formValuesSetter: (fieldValues: string[]) => void;
};

export function BranchCreateModal({
	handleModalOpen,
	open,
	onPopupClose,
	hasToShowGuard,
	formValuesSetter
}: Props) {
	const closeModal = useCallback(() => handleModalOpen(false), []);
	const { companyId } = useAppState();

	return (
		<div className="relative">
			<WhiteModal handleModalOpen={onPopupClose} open={open} scrollable noPadding>
				<EntityForm.Main
					entity="branches"
					name={`AllBranches-${companyId}`}
					url={`/${companyId}/my/company-branches/`}
					method="post"
					primaryKey="id"
					sendAsFormData={false}
					normalizeData={(data: any) => data}
					prependData
					fields={[
						{
							name: 'name',
							required: true,
							value: '',
							onSubmitKey: ''
						},
						{
							name: 'address',
							required: true,
							value: '',
							onSubmitKey: ''
						},
						{
							name: 'city',
							required: true,
							value: '',
							onSubmitKey: ''
						},
						{
							name: 'phone',
							value: '',
							onSubmitKey: ''
						},
						{
							name: 'state_name',
							required: false,
							value: '',
							onSubmitKey: ''
						},
						{
							name: 'country_name',
							required: true,
							value: '',
							onSubmitKey: ''
						},
						{
							name: 'zipcode',
							value: '',
							onSubmitKey: ''
						},
						{
							name: 'lat',
							value: '',
							type: 'number',
							onSubmitKey: '',
							onSubmitValue: (value) => Number(value).toFixed(7)
						},
						{
							name: 'lng',
							value: '',
							type: 'number',
							onSubmitValue: (value) => Number(value).toFixed(7),
							onSubmitKey: ''
						}
					]}
					onSuccess={(response, resetForm): any => {
						// console.log(response);
						closeModal();
						resetForm();
					}}
					onError={(error, resetForm): any => {
						// console.log(error);
						resetForm();
					}}
				>
					{({ isSubmitting, values, setFieldValue, errors, setFieldError, resetForm }) => {
						return (
							<BranchFormElements
								values={values}
								setFieldValue={setFieldValue}
								setFieldError={setFieldError}
								isSubmitting={isSubmitting}
								errors={errors}
								resetForm={resetForm}
								formValuesSetter={formValuesSetter}
								closeModal={hasToShowGuard}
							/>
						);
					}}
				</EntityForm.Main>
			</WhiteModal>
		</div>
	);
}
