import { MdAddCircleOutline, MdModeEdit } from 'react-icons/md';
import {
	ConfirmModal,
	KntButton,
	Loader,
	Locations,
	StyledTable,
	StyledTableCell,
	StyledTableHeadCell,
	StyledTableRow
} from 'shared/components';
import EntityContainer from 'modules/entity/containers';
import MuiIcons, {
	Pagination,
	TableBody,
	TableContainer,
	TableHead
} from 'shared/components/Elements';
import React, { memo, useCallback, useState } from 'react';
import { useAppState } from 'shared/state';
import { useFormGuard, usePagination } from 'shared/hooks';
import { Branch } from '../../../BusinessManagement/components/BusinessLocations';
import { RiDeleteBin7Fill } from 'react-icons/ri';
import { AiFillEye } from 'react-icons/ai';
import 'react-sliding-pane/dist/react-sliding-pane.css';
import {
	BranchCreateModal,
	BranchDeleteModal,
	BranchEditModal,
	BranchViewModal
} from './components';
import SlidingPane from 'react-sliding-pane';
import { useAuth } from 'modules/auth/hooks/useAuth';
import { Company } from 'modules/user/reducers';
import clsx from 'clsx';
import { useTranslation } from 'react-i18next';

export const Branches = memo(function Branches() {
	const { t } = useTranslation();
	const { companyId, isPermitted } = useAppState();
	const { company } = useAuth();
	const { page, handlePage } = usePagination();
	const [modifyModal, setModifyModal] = useState<Branch | null>(null);
	const [deleteModal, setDeleteModal] = useState<Branch | null>(null);
	const [createModal, setCreateModal] = useState(false);
	const [viewModal, setViewModal] = useState<Branch | null>(null);
	const [paneOpen, setPaneOpen] = useState(false);

	const handleModal = useCallback((item: Branch | null) => setModifyModal(item), []);
	const handleDeleteModal = useCallback((item: Branch | null) => setDeleteModal(item), []);
	const handleViewModal = useCallback((item: Branch | null) => setViewModal(item), []);
	const handleCreateModal = useCallback(() => setCreateModal((prev) => !prev), []);
	const handleMapPane = useCallback(() => {
		setPaneOpen((prev) => !prev);
	}, []);
	const { formValues, guardOpen, setGuardOpen, formValuesSetter } = useFormGuard();

	const confirmToggleHandler = useCallback(() => {
		setGuardOpen((prev: boolean) => !prev);
	}, []);
	const confirmModalHandler = useCallback(() => {
		setGuardOpen(false);
		formValuesSetter([]);
		handleCreateModal();
	}, [createModal]);

	const hasToShowGuard = useCallback(() => {
		if (formValues.length) {
			setGuardOpen(true);
			return;
		}

		handleCreateModal();
	}, [formValues]);

	const onPopupClose = () => {
		if (formValues.length) {
			setGuardOpen(true);
			return;
		}
		handleCreateModal();
	};

	return (
		<div className="branches-page pt-8">
			<div className=" mb-7 items-center justify-between lg:flex">
				<h1 className=" mb-4 text-lg font-semibold dark:text-white lg:mb-0">Branch List</h1>
				<div className="buttons-row flex items-center ">
					<KntButton className="mr-4" onClick={handleMapPane}>
						{t('show_map')}
					</KntButton>
					<KntButton
						isDisabled={!isPermitted}
						color={'transparent'}
						iconComponent={<MdAddCircleOutline />}
						iconClassNames="text-primary-dark text-xl"
						onClick={handleCreateModal}
					>
						{t('new_branch')}
					</KntButton>
				</div>
			</div>
			<div className="branches-page__list">
				{companyId ? (
					<EntityContainer.All
						entity="branches"
						name={`AllBranches-${companyId}`}
						url={`/${companyId}/my/company-branches/`}
						params={{
							limit: 10,
							page
						}}
					>
						{({ items, isFetched, meta }) => {
							const branches = items.map((item: unknown) => item) as Branch[];
							return (
								<div>
									<TableContainer classes={{ root: 'relative w-full' }}>
										<StyledTable
											aria-label="simple table"
											classes={{
												root: 'table-fixed min-w-[1050px] md:min-w-full dark-mode-styled-table'
											}}
										>
											<TableHead>
												<StyledTableRow>
													<StyledTableHeadCell style={{ width: '5%' }}>#</StyledTableHeadCell>
													<StyledTableHeadCell style={{ width: '30%' }}>
														{t('branch')}
													</StyledTableHeadCell>
													<StyledTableHeadCell>{t('address')}</StyledTableHeadCell>
													<StyledTableHeadCell>{t('phone_number')}</StyledTableHeadCell>
													<StyledTableHeadCell className="!text-center">
														{t('actions')}
													</StyledTableHeadCell>
												</StyledTableRow>
											</TableHead>
											<TableBody>
												{branches.map((item: Branch, idx: number) => {
													return (
														<StyledTableRow key={item.id}>
															<StyledTableCell component="th" scope="row">
																{`${idx + 1}.`}
															</StyledTableCell>
															<StyledTableCell classes={{ root: 'truncate' }}>
																{item.name}
															</StyledTableCell>
															<StyledTableCell>{item?.address}</StyledTableCell>
															<StyledTableCell>{item?.phone}</StyledTableCell>
															<StyledTableCell>
																<div className="flex justify-center text-kgrey-dark-2">
																	<div className={clsx({ 'cursor-not-allowed': !isPermitted })}>
																		<MuiIcons.IconButton
																			disabled={!isPermitted}
																			aria-label="delete"
																			color="inherit"
																			onClick={() => handleDeleteModal(item)}
																		>
																			<RiDeleteBin7Fill />
																		</MuiIcons.IconButton>
																	</div>
																	<div className={clsx({ 'cursor-not-allowed': !isPermitted })}>
																		<MuiIcons.IconButton
																			aria-label="view"
																			color="primary"
																			onClick={() => handleViewModal(item)}
																		>
																			<AiFillEye />
																		</MuiIcons.IconButton>
																	</div>
																	<div className={clsx({ 'cursor-not-allowed': !isPermitted })}>
																		<MuiIcons.IconButton
																			disabled={!isPermitted}
																			aria-label="edit"
																			color="inherit"
																			onClick={() => handleModal(item)}
																		>
																			<MdModeEdit />
																		</MuiIcons.IconButton>
																	</div>
																</div>
															</StyledTableCell>
														</StyledTableRow>
													);
												})}
											</TableBody>
										</StyledTable>
										{!isFetched && <Loader tiled withOverlay />}
									</TableContainer>
									<BranchDeleteModal open={deleteModal} handleModalOpen={handleDeleteModal} />
									<BranchCreateModal
										hasToShowGuard={hasToShowGuard}
										onPopupClose={onPopupClose}
										formValuesSetter={formValuesSetter}
										handleModalOpen={handleCreateModal}
										open={createModal}
									/>
									<BranchEditModal handleModalOpen={handleModal} open={modifyModal} />
									<BranchViewModal handleModalOpen={handleViewModal} open={viewModal} />
									<ConfirmModal
										featureHandler={confirmModalHandler}
										isOpen={guardOpen}
										toggleHandler={confirmToggleHandler}
										title={t('cancel_unsaved_changes_confirmation')}
									/>

									<div className="mt-4 mb-4">
										<Pagination count={meta?.last_page} page={page || 1} onChange={handlePage} />
									</div>
									{branches.length ? (
										<SlidingPane
											className="some-custom-class"
											overlayClassName="some-custom-overlay-class"
											isOpen={paneOpen}
											title="Map"
											from="bottom"
											width="100%"
											onRequestClose={handleMapPane}
										>
											<Locations branches={branches} currentCompany={company as Company} />
										</SlidingPane>
									) : null}
								</div>
							);
						}}
					</EntityContainer.All>
				) : (
					<Loader />
				)}
			</div>
		</div>
	);
});
